import React from "react";
import RenderSortButton from "./RenderSortButton";
import {AllPostsActions} from "../../../redux/reducers/all-posts-reducer";
import {useDispatch, useSelector} from "react-redux";
import {GlobalStateType} from "../../../redux/store/store-redux";
const { setSortHeaderDirectionAC} = AllPostsActions // извлекаем из экшен креатор на получение комментариев

const RenderSortButtonContainer:React.FC = () => {

    const dispatch = useDispatch()

    // Направление сортировки по постам
    const SortHeaderDirection: boolean | undefined = useSelector( (state: GlobalStateType) => state.allPosts.SortHeaderDirection ) // направление сортировки

    //колбек смены направления сортировки по постам
    const setSortHeaderDirection = (SortHeaderDirection: boolean | undefined) => { // задание направления сортировки в стейт
        dispatch( setSortHeaderDirectionAC( SortHeaderDirection ) )
    }

    return <div>
        <RenderSortButton // отрисовка кнопки сортировки
            SortHeaderDirection={SortHeaderDirection} setSortHeaderDirection={setSortHeaderDirection}/>
    </div>
}
export default RenderSortButtonContainer